import React,{Component} from "react"
import Navigation from './components/navigation.js'
import {menus} from './menus.json'
import Formulario from './components/Formulario.js'
import Seccion from './components/Seccion.js'
import {
  Container,
  Button
} from "react-bootstrap";

class App extends Component{
  constructor(){
    super();
    this.state={
      menus,
      nombreRestaurant:"Nombre del Restaurant"
    }
    this.handleAdd=this.handleAdd.bind(this)
    this.handleRemove=this.handleRemove.bind(this)
    this.handleEdit=this.handleEdit.bind(this)
    this.downloadMenu=this.downloadMenu.bind(this)
    this.downloadOptionals=this.downloadOptionals.bind(this)
    this.downloadProducts=this.downloadProducts.bind(this)
    this.handleInput=this.handleInput.bind(this)
   
  }
  handleInput(e){
    e.preventDefault()
    const {value, name} =e.target 
    if(value!==""){
    this.setState({[name]: value})
  }}

  handleAdd(group,example,ids,idp,idg){
    if(group==="seccion"){
      if(example.nombreSeccion!==""){
      this.setState(prevState=> (prevState.menus.push(example)))}else{
      alert('Debes cargar el nombre de la sección');
      return;}
  }else{if(group==="producto"){
      if(example.nombreProducto!=="" & example.precio!==""& example.descripcion!==""){
      const indexs = this.state.menus.findIndex((item) => item.ids === ids)
      this.setState(prevState=> (prevState.menus[indexs].productos.push(example)))}else{
        alert('Debes cargar el nombre del producto, la descripción y el precio mismo');
        return;}
    }else{if(group==="grupoOpciones"){
      if(example.nombreGrupo!=="" & example.cantidadMinima!==""& example.cantidadMaxima!==""){
      const indexs = this.state.menus.findIndex((item) => item.ids === ids)
      const indexp = this.state.menus[indexs].productos.findIndex((item) => item.idp === idp)
      this.setState(prevState=> (prevState.menus[indexs].productos[indexp].grupoOpciones.push(example)))}else{
        alert('Debes cargar el nombre del grupo de Opciones (Ejemplo: "Extras"), la cantidad minima (puede ser cero en caso de no ser obligatorio la seleccion de una opción), la cantidad maxima y si modifica precio (si selecciona el valor definido en la opcion sera el precio total, si no selecciona, el valor de la opcion se adicionara al precio.) ');
        return;}
    }else{if(group==="opcion"){
      if(example.nombreOpcion!=="" & example.precioAdicional!==""){
      const indexs = this.state.menus.findIndex((item) => item.ids === ids)
      const indexp = this.state.menus[indexs].productos.findIndex((item) => item.idp === idp)
      const indexg=this.state.menus[indexs].productos[indexp].grupoOpciones.findIndex((item) => item.idg === idg)
      this.setState(prevState=> (prevState.menus[indexs].productos[indexp].grupoOpciones[indexg].opciones.push(example)))}else{
        alert('Debes cargar el nombre de la opción y el precio de la misma. (Puede ser Cero en caso no tener valor adicional))');
        return;}
  }
  }}}}
  handleRemove(group,ids,idp,idg,ido){
    if(group==="seccion"){
    if( window.confirm("Cuidado al borrar la seccion, se borraran todos los productos que contiene.¿Seguro quieres borrar la Seccion?")){
      const indexs = this.state.menus.findIndex((item) => item.ids === ids)
      this.setState({
      menus: this.state.menus.filter((e,i)=>{
        return i !== indexs
      })
    })}}else{if(group==="producto"){
    
        const indexs = this.state.menus.findIndex((item) => item.ids === ids)
        const indexp = this.state.menus[indexs].productos.findIndex((item) => item.idp === idp)
        this.setState(this.state.menus[indexs].productos.splice(indexp, 1));
        
      }else{if(group==="grupoOpciones"){
        if( window.confirm("¿Seguro quieres borrar este Grupo de Opciones y sus Opcionales?")){
        const indexs = this.state.menus.findIndex((item) => item.ids === ids)
        const indexp = this.state.menus[indexs].productos.findIndex((item) => item.idp === idp)
        const indexg=this.state.menus[indexs].productos[indexp].grupoOpciones.findIndex((item) => item.idg === idg)
        this.setState(this.state.menus[indexs].productos[indexp].grupoOpciones.splice(indexg, 1));}
      }else{if(group==="opcion"){
        if( window.confirm("¿Seguro quieres borrar esta Opción?")){
        const indexs = this.state.menus.findIndex((item) => item.ids === ids)
        const indexp = this.state.menus[indexs].productos.findIndex((item) => item.idp === idp)
        const indexg=this.state.menus[indexs].productos[indexp].grupoOpciones.findIndex((item) => item.idg === idg)
        const indexo=this.state.menus[indexs].productos[indexp].grupoOpciones[indexg].opciones.findIndex((item) => item.ido === ido)
        this.setState(prevState=> (prevState.menus[indexs].productos[indexp].grupoOpciones[indexg].opciones.splice(indexo, 1)))
    }}else{if(group==="todo"){
      if( window.confirm("CUIDADO- ¿Seguro quieres borrar todos las secciones y todos los productos cargados? Comenzarás a cargar tu menu sin ninguna guia")){
        this.setState(prevState=> (prevState.menus=[]))
      }}
  }}}}
  }
  handleEdit(group,example,ids,idp,idg,ido){
    if(group==="seccion"){
        const indexs = this.state.menus.findIndex((item) => item.ids === ids)   
        this.setState(prevState=>prevState.menus[indexs].nombreSeccion=example) 
    }else{if(group==="producto"){
      const indexs = this.state.menus.findIndex((item) => item.ids === ids)
      const indexp = this.state.menus[indexs].productos.findIndex((item) => item.idp === idp)  
      this.setState(prevState=>prevState.menus[indexs].productos[indexp].nombreProducto=example.nombreProducto)
      this.setState(prevState=>prevState.menus[indexs].productos[indexp].descripcion=example.descripcion)
      this.setState(prevState=>prevState.menus[indexs].productos[indexp].precio=example.precio)
    }else{if(group==="grupoOpciones"){
      const indexs = this.state.menus.findIndex((item) => item.ids === ids)
      const indexp = this.state.menus[indexs].productos.findIndex((item) => item.idp === idp)
      const indexg=this.state.menus[indexs].productos[indexp].grupoOpciones.findIndex((item) => item.idg === idg)  
      this.setState(prevState=>prevState.menus[indexs].productos[indexp].grupoOpciones[indexg].nombreGrupo=example.nombreGrupo)
      this.setState(prevState=>prevState.menus[indexs].productos[indexp].grupoOpciones[indexg].cantidadMinima=example.cantidadMinima)
      this.setState(prevState=>prevState.menus[indexs].productos[indexp].grupoOpciones[indexg].cantidadMaxima=example.cantidadMaxima)
      this.setState(prevState=>prevState.menus[indexs].productos[indexp].grupoOpciones[indexg].modificaPrecio=example.modificaPrecio)
    }else{if(group==="opcion"){
  
      const indexs = this.state.menus.findIndex((item) => item.ids === ids)
      const indexp = this.state.menus[indexs].productos.findIndex((item) => item.idp === idp)
      const indexg=this.state.menus[indexs].productos[indexp].grupoOpciones.findIndex((item) => item.idg === idg)
      const indexo=this.state.menus[indexs].productos[indexp].grupoOpciones[indexg].opciones.findIndex((item) => item.ido === ido)  
      this.setState(prevState=>prevState.menus[indexs].productos[indexp].grupoOpciones[indexg].opciones[indexo]=example)
  }
  }}}}
  downloadProducts() {
  const prueba= this.state.menus.map((seccion)=>{
  return [seccion.nombreSeccion,seccion.productos.map((producto)=>{return producto.idp}),seccion.productos.map((producto)=>{return producto.nombreProducto}),seccion.productos.map((producto)=>{return producto.precio}),seccion.productos.map((producto)=>{return producto.descripcion})]
  })
  
  let productoi=[]
  for(let i=0;i<prueba.length;i++){
    for(let a=0;a<prueba[i][1].length;a++){
      let productof=[prueba[i][0],// Nombre Seccion
      prueba[i][1][a],// IDP
      prueba[i][2][a],// Nombre Producto
      prueba[i][3][a],// Precio del Producto
      "",// Imagen del Producto
      prueba[i][4][a]// Descripcion Producto
      ,""// Unidad de Medida
      ,""// Cantidad de Unidades
      ,""// Impuesto
      ,""// Id del Producto en catalogo
      ,""// Indice
    ]
      productoi.push(productof)
    }  
  }
  let csvContent = "data:text/csv;charset=utf-8,";
  productoi.forEach(function(rowArray) {
    let row = rowArray.join(";");
    csvContent += row + "\r\n";
});
var encodedUri = encodeURI(csvContent);
var link = document.createElement("a");
link.setAttribute("href", encodedUri);

link.setAttribute("download", this.state.nombreRestaurant+" Menu.csv");
document.body.appendChild(link); // Required for FF
link.click();
  }
  downloadOptionals() {
    const prueba= this.state.menus.map((seccion)=>{
    return [seccion.productos.map((producto)=>{return producto.idp}),seccion.productos.map((producto)=>{return producto.grupoOpciones.map((grupo)=>{return grupo.idg})}),
      seccion.productos.map((producto)=>{return producto.grupoOpciones.map((grupo)=>{return grupo.nombreGrupo})}),
      seccion.productos.map((producto)=>{return producto.grupoOpciones.map((grupo)=>{return grupo.cantidadMaxima})}),
      seccion.productos.map((producto)=>{return producto.grupoOpciones.map((grupo)=>{return grupo.cantidadMinima})}),
      seccion.productos.map((producto)=>{return producto.grupoOpciones.map((grupo)=>{return grupo.modificaPrecio})}),
      seccion.productos.map((producto)=>{return producto.grupoOpciones.map((grupo)=>{return grupo.opciones.map((opciones)=>{return opciones.nombreOpcion})})}),
      seccion.productos.map((producto)=>{return producto.grupoOpciones.map((grupo)=>{return grupo.opciones.map((opciones)=>{return opciones.precioAdiconal})})}),
      seccion.productos.map((producto)=>{return producto.grupoOpciones.map((grupo)=>{return grupo.opciones.map((opciones)=>{return opciones.ido})})})]
    })
    let productoi=[]
    for(let is=0;is<prueba.length;is++){
      for(let ip=0;ip<prueba[is][0].length;ip++){
        if(prueba[is][1][ip]!==[]){  
        for(let ig=0;ig<prueba[is][1][ip].length;ig++){
          if(prueba[is][1][ip][ig]!==[]){  
          for(let io=0;io<prueba[is][6][ip][ig].length;io++){
              let opcion=[prueba[is][0][ip],// SKU producto
              prueba[is][2][ip][ig],// Nombre del Grupo de Opcion
              prueba[is][1][ip][ig],// idg
              prueba[is][2][ip][ig],// Nombre del Grupo de Opcion
              prueba[is][4][ip][ig],// Cantidad Minimanpm 
              prueba[is][3][ip][ig],// Cantidad Maxima
              prueba[is][6][ip][ig][io],// Nombre Opcion
              prueba[is][7][ip][ig][io],// Precio Opcion
              prueba[is][8][ip][ig][io],// ido
              prueba[is][6][ip][ig][io],// Nombre Opcion
              prueba[is][5][ip][ig]]// ModificaPrecio
              productoi.push(opcion)}}
          }}}
  }
  let csvContent = "data:text/csv;charset=utf-8,";
  productoi.forEach(function(rowArray) {
    let row = rowArray.join(";");
    csvContent += row + "\r\n";
});
var encodedUri = encodeURI(csvContent);
var link = document.createElement("a");
link.setAttribute("href", encodedUri);
link.setAttribute("download", "Opcionales.csv");
document.body.appendChild(link); // Required for FF
link.click();
    }
    downloadMenu() { 
      this.downloadProducts()
      // this.downloadOptionals() No habilitado descargar opcionales

      }

  downloadMenus() { 
        const jsonData=this.state
        let dataStr = JSON.stringify(jsonData);
        let dataUri = 'data:application/json;charset=utf-8,'+ encodeURIComponent(dataStr);
        let exportFileDefaultName = 'Menu.json';
        let linkElement = document.createElement('a');
        linkElement.setAttribute('href', dataUri);
        linkElement.setAttribute('download', exportFileDefaultName);
        linkElement.click();

      }
  render() {
   
    return (
    
    <div  style={{textAlign: "center"}}  onSubmit={e => { e.preventDefault(); }} >
        <Navigation titulo= "Generador de Menu" cantidadSecciones={this.state.menus.length}/>
  
    <Container > 
   <div style={{padding: "0.5rem"}}>
        <h6>Bienvenido al Generador Inicial de Menu, acá podras cargar todos tus productos.</h6>
        <h6>A modo de guia, pre-cargamos algunas secciones con los productos más comunes. </h6>
        <h6>Editalas o Borralas según se ajusten a tu negocio. Al terminar, haz click en "Descargar Menu" y comparte el archivo con tu Asesor</h6>
    
    <input type="text" style={{textAlign: "center"}} onChange={this.handleInput} name="nombreRestaurant" className="form-control" placeholder={this.state.nombreRestaurant}></input>
   
    </div>
    <div style={{padding: "0.5rem"}}>
      <Button variant="danger" size="sm" block onClick={this.handleRemove.bind(this,"todo")}>Borrar Todo</Button>
       {this.state.menus.map((seccion)=> {return <Seccion key={seccion.ids} seccion={seccion} add={this.handleAdd} edit={this.handleEdit} cambios={this.changeSeccion} cambiop={this.changeProducto} remove={this.handleRemove}/>})}
  
    <Formulario add={this.handleAdd}/>
  
    </div>
    </Container>
    <Container style={{padding: "1rem"}}>
    <button className="btn btn-success" onClick={this.downloadMenu.bind(this)}>
                  Descargar Menu
            </button>
    </Container>
    </div>
   
  );
  }
}
  

export default App;
