import React, { Component} from "react"

import {
  Container,

  Button,
  Modal,
  Form,

  Row,
  Col
} from "react-bootstrap";



class Producto extends Component{
  constructor(){
    super();
    this.state={
    nombreProducto:"",
    descripcion:"",
    precio:"",
    nombreGrupo:"",
    cantidadMinima:"",
    cantidadMaxima:"",
    modalActualizar: false,
    mostrarOpciones:false,
    modificaPrecio:0}
  this.handleInput=this.handleInput.bind(this)
  this.handleSubmit=this.handleSubmit.bind(this)
  this.modalActualizar=this.modalActualizar.bind(this)
 
  
 }  
    handleInput(e){
    const {value, name} =e.target 
    if(value!==""){
      this.setState({
        [name]: value})}}

    handleSubmit(ids,idp){
      const id=Math.floor(Math.random() * 10000) + 1
      const nuevogrupo={idg:id,nombreGrupo: this.state.nombreGrupo,cantidadMinima:this.state.cantidadMinima,cantidadMaxima:this.state.cantidadMaxima,modificaPrecio:this.state.modificaPrecio,opciones:[]}
      this.props.add("grupoOpciones",nuevogrupo,ids,idp)
    }    
    modalActualizar(show,producto){
        if(show){
        this.setState({
        modalActualizar: show,
        nombreProducto: producto.nombreProducto,
        precio: producto.precio,
        descripcion: producto.descripcion
      })}else{
        this.setState({
          modalActualizar: show
        })

      }};
      ;
    render(){ 
        
return(<Container>
  <Row style={{padding: "0.2rem"}}> 
  <Col><h5>{this.props.producto.nombreProducto}</h5></Col> 
  <Col><h5>Precio: {this.props.producto.precio}</h5></Col>
  <Col>
  <Button variant="primary" size="sm" onClick={this.modalActualizar.bind(this,true,this.props.producto)}>Editar</Button>{' '}
  <Button variant="danger" size="sm" onClick={this.props.remove.bind(this,"producto",this.props.ids,this.props.producto.idp)}>Borrar</Button> </Col> 
  </Row >
  <Row>
  <p>Descripción: {this.props.producto.descripcion}</p>
  </Row>
<Modal show={this.state.modalActualizar} >
<Modal.Header><div><h3>Editar Registro</h3></div> </Modal.Header>
<Modal.Body ><Form.Group>
    <label>Nombre Producto: </label>
    <input className="form-control" name="nombreProducto" type="text" onChange={this.handleInput} placeholder={this.props.producto.nombreProducto}/>
  </Form.Group>
  <Form.Group>
    <label>Descripcion: </label>
    <input className="form-control"name="descripcion"type="text"onChange={this.handleInput}placeholder={this.props.producto.descripcion}/>
  </Form.Group>
  <Form.Group>
    <label>Precio: </label>
    <input className="form-control"name="precio" type="number" min="0"onChange={this.handleInput}placeholder={this.props.producto.precio}/>
  </Form.Group>
</Modal.Body>
<Modal.Footer>
  <Button variant="success"onClick={() => {this.props.edit("producto",this.state,this.props.ids,this.props.producto.idp);this.modalActualizar(false)}}>Editar</Button>
  <Button variant="danger"onClick={() => this.modalActualizar(false)}>Cancelar</Button>
</Modal.Footer>
</Modal>

</Container>)

}}
export default Producto;